@import "./../../AppVariables";

.userCard {
    display: grid;
    grid-template-areas: "leftContainer rightContainer";
    // grid-template-rows: 1fr 3fr
    grid-template-columns: 100px 1fr;
    grid-gap: $padding;
    margin-bottom: $padding; }

.leftContainer {
    grid-area: leftContainer; }

.rightContainer {
    grid-area: rightContainer; }

.imgDiv {
    width: 100px;
    height: 100px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 3px; }




.fullNameDiv {
    font-weight: bolder; }

.statusDiv {
    color: lightgray; }

.countryDiv {
    padding-left: 5px; }

.followBtn {
    width: 100%;
    background: royalblue;
    border: none;
    border-radius: 3px;
    padding: 5px;
    color: white;
    margin-top: 5px; }


.followedBtn {
    background-color: #bfbfbf; }

.followBtn:disabled, .followedBtn:disabled {
    background: #afead6; }

.paginationContainer {
    margin-bottom: $padding;
    width: 100%;
    text-align: center; }

.paginationContainer {
    .paginationBtn, .paginationInput {
        padding: ($padding / 5) ($padding / 2);
        background: #c8d5dc;
        display: inline-flex;
        margin: 5px;
        margin-left: 0;
        margin-right: 5px;
        border-radius: 5px;
        cursor: pointer;
        color: white;
        position: relative; }
    .paginationInput {
        background: none;
        border: 2px solid #c8d5dc;
        color: initial;
        width: 30px;
        font-size: initial;
        transform: translateY(-1px); } }

.paginationBtn {
    user-select: none; }

.paginationContainer > .paginationBtn:first-of-type {
    margin-left: 0; }

.paginationBtn.activeBtn {
    background: #FF9800; }

.firstButton:after {
    content: '...'; }
.lastButton:before {
    content: '...'; }

